.mainDoctor {
  display: flex;

  width: 63%;
  margin: 80px auto 20px auto;
  background: #f2f2f2;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  position: relative;
  .newVideo {
    display: flex;
    align-items: center;
    background: #f2f2f2;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    position: absolute;

    top: -40px;
    left: 0;
    height: 40px;
    width: 250px;
    img {
      margin: 0 10px 0 15px;
    }
    p {
      margin: 0;
      font-size: 15px;
      font-weight: 450 !important;
      color: #666666;
    }
  }

  .doctor {
    width: 50%;
    padding: 20px 20px 20px 15px;
    margin: 20px 0 20px 0;

    .doctor-p {
      display: flex;
      flex-direction: column;
      input[type="number"]::-webkit-inner-spin-button,
      input[type="number"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
      }
      input {
        height: 40px;
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        margin: 2px 0 20px 0;
        text-indent: 15px;
        font-weight: 700;
        width: 90%;

        &::placeholder {
          font-weight: 700;
          color: #969696;
        }
      }
      label {
        color: #666666;
        text-align: left;
      }

      .saveChanges {
        background: #00aff0;
        width: 100px;
        height: 35px;
        color: white;
        border: none;
        border-radius: 10px;
        font-weight: 600;
        margin: 140px 0 0 0;
      }
    }
  }

  .form {
    padding: 20px 15px 20px 20px;
    margin: 20px 0 20px 0;
    width: 50%;
    color: #666666;
    .pricing {
      font-weight: 700;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 0 30px 0;
    }
    .emailVisit {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin: 20px 0;
      input[type="number"]::-webkit-inner-spin-button,
      input[type="number"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
      }

      input {
        height: 40px;
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        margin: 0 0 0 auto;

        text-indent: 15px;
        font-weight: 700;
        width: 100px;
        &::placeholder {
          color: #969696;
        }
      }
      // .css-2b097c-container {
