    .allergies {
      text-align: start;
      font-weight: 700;
      color: #666666;
      display: flex;
      flex-direction: column;
      // margin: 40px 0 0 0;
      height: 290px;
      .Nameing {
        display: flex;
        justify-content: space-between;
        p {
          margin-bottom: 7px;
        }
      }

      .textAndProfile {
        // display: flex;
        // justify-content: flex-end;
        // flex-direction: row;
        height: 100%;

        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        // margin: 7px 0 0 0;
        padding: 10px 15px;
        font-weight: 700;
        width: 100%;
        overflow-y: scroll;
        resize: none;

        &::-webkit-scrollbar {
          width: 5px;
        }

        &::-webkit-scrollbar-button:single-button {
          // background-color: #bbbbbb;
          display: block;

          height: 13px;
          width: 16px;
        }

        &::-webkit-scrollbar-track {
          background: #f1f1f1;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb {
          background: #fa9551;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #db8246;
        }

        &::placeholder {
          font-weight: 700;
          color: #969696;
