        .profilePic {
          display: flex;
          float: right;
          // margin: 7px 0 20px 20px;
          width: 100px;
          height: 120px;

          p {
            font-weight: 600;
            color: #666666;
            margin: 0;
          }
          .upload-btn-wrapper {
            position: relative;
            overflow: hidden;
            .btn {
              margin: 0;
              border: 1.7px solid #666666;
              width: 100px;
              height: 120px;
              padding: 0;
              background: transparent !important;
              .docImage {
                width: 95px;
                height: 116px;
                object-fit: cover;
                // border-radius: 50%;
              }
              p {
                font-size: 80px;
                padding: 0;
              }
            }
            .deleteImage {
              position: absolute;
              width: 20px;
              height: 20px;
              padding-top: 1px;
              background: red;
              color: white;
              border: none;
              border-radius: 5px;
              z-index: 1000;
              top: 5px;
              left: 75px;
            }
          }

          .upload-btn-wrapper input[type="file"] {
            font-size: 0px;
            position: absolute;

            left: 0;
            top: -20px;
            width: 100px;
            height: 140px;
            opacity: 0;
            cursor: pointer;
          }
        }
        .changeDiv {
          height: 100%;
          color: #969696;
        }
      }
    }
    label {
      color: #666666;
      text-align: left;
      width: 100%;
    }
    .workHoursDiv {
      display: flex;
      justify-content: flex-start;
      width: 90%;
      margin-top: 5px;
      color: #666666;
      input {
        margin: 0;
        font-size: 15px;
        text-align: center;
        width: 100px;
        text-indent: 0;
        &::placeholder {
          // color: blue;
          color: #969696;

          text-align: center;
        }
        height: 40px;
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        font-weight: 700;
      }
      input[type="time"]::-webkit-calendar-picker-indicator {
        background: none;
        display: none;
      }
      // input::-webkit-datetime-edit-ampm-field {
