.mainClien {
  display: flex;

  width: 63% !important;
  margin: 80px auto 20px auto;
  background: #f2f2f2;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  position: relative;
  padding: 0 !important;
  .newVideo {
    display: flex;
    align-items: center;
    background: #f2f2f2;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    position: absolute;
    font-size: 15px;

    top: -40px;
    left: 0;
    height: 40px;
    width: 250px !important;
    img {
      margin: 0 10px 0 15px;
    }
    p {
      margin: 0;
      font-size: 15px;
      font-weight: 450 !important;
      color: #666666;
    }
  }

  .client {
    width: 50%;

    padding: 20px 20px 20px 15px !important;
    margin: 20px 0 20px 0;

    .client-p {
      display: flex;
      flex-direction: column;
      input[type="number"]::-webkit-inner-spin-button,
      input[type="number"]::-webkit-outer-spin-button {
        -webkit-appearance: none;
      }

      input {
        height: 40px;
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        margin: 2px 0 25px 0;
        text-indent: 15px;
        font-weight: 700;

        &::placeholder {
          font-weight: 700;
          color: #969696;
        }
      }
      label {
        color: #666666;
        text-align: left;
      }

      .saveChanges {
        background: #00aff0;
        width: 100px;
        height: 35px;
        color: white;
        border: none;
        border-radius: 10px;
        font-weight: 600;
        margin: 25px 0 0 0;
      }
    }
  }

  .form {
    padding: 20px 15px 20px 20px !important;
    margin: 20px 0 20px 0;
    width: 50%;
    .conditions {
      display: flex;
      flex-direction: column;
      text-align: start;
      font-weight: 700;
      color: #666666;
      textarea {
        height: 190px;
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        padding: 10px 15px;
        background: white;
        margin: 5px 0 10px 0;
        font-weight: 700;
        resize: none;

        &::-webkit-scrollbar {
          width: 5px;
        }

        &::-webkit-scrollbar-button:single-button {
          // background-color: #bbbbbb;
          display: block;

          height: 13px;
          width: 16px;
        }

        &::-webkit-scrollbar-track {
          background: #f1f1f1;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb {
          background: #fa9551;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #db8246;
        }

        &::placeholder {
          font-weight: 700;
          color: #969696;
        }
      }
    }
    .allergies {
      display: flex;
      flex-direction: column;
      text-align: start;
      font-weight: 700;
      color: #666666;
      textarea {
        border: 1.7px solid #fa9551;
        border-radius: 10px;
        background: white;
        margin: 5px 0 20px 0;
        padding: 10px 15px;
        font-weight: 700;
        height: 190px !important;
        resize: none;
        &::-webkit-scrollbar {
