        }

        &::-webkit-scrollbar-track {
          background: #f1f1f1;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb {
          background: #fa9551;
          border-radius: 5px;
        }

        &::-webkit-scrollbar-thumb:hover {
          background: #db8246;
        }

        &::placeholder {
          font-weight: 700;
          color: #969696;
        }
      }
    }
    .checkboxDiv {
      display: flex;
