        input[type="checkbox"] {
          visibility: hidden;
          position: absolute;
        }

        input[type="checkbox"] + label:before {
          height: 15px;
          width: 15px;
          margin-right: 5px;
          content: " ";
          display: inline-block;
          vertical-align: baseline;
          border: 1px solid #00aff0;
          border-radius: 50%;
        }

        input[type="checkbox"]:checked + label:before {
